.contactHead {
    color: white;
}

.contact-section {
    background-color: rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
}

.contact-info {
    display: flex;
    padding-bottom: 50px;
    max-width: 1300px;
}

.map-img {
    width: 50%;
    margin: auto;
}

.icon-c {
    width: auto;
    height: 30px;
}

.contact-text {
    flex: 1 1;
    padding-left: 20px;
}

.contact-map {
    flex: 1 1;
    display: flex;
    align-items: center;
    text-align: center;
}

.info {
    display: flex;
    align-items: center;
    margin: 20px;
}

.info .text {
    padding-left: 20px;
}

.contact-info .contact-text .social-media {
    display: flex !important;
    justify-content: center;
}

.social-media .icon-c {
    margin-right: 20px ;
}

.social-media .container {
    display: flex;
}

.container {
    justify-content: flex-end;
    margin-right: 50px;
}

@media only screen and (max-width: 680px) {
    .contact-info {
        flex-direction: column;
        max-width: 80%;
    }
    .ctext {
        text-align: justify;
    }
    .map-img {
        margin-top: 40px;
        width: 90%;
    }
    .container {
        justify-content: flex-end;
    }
}


